import React, { useState, useEffect } from "react";
// react-helmet
import { Helmet } from "react-helmet";

// components imports
// import Header from '../../../Components/Blogs/Header';
import Header from "../../WelcomePages/components/Header";
import BlogDetails from "../../../Components/Blogs/Blogs/BlogDetails";
import Footer from "../../WelcomePages/components/Footer";
import BlogList from "../../../Components/Blogs/Blogs/BlogsList";
import SignUp from "../SignUp";

// classes
import useStyles from "./styles/muiStyles";

// request helpers
import { FetchBlogsRecent, FetchBlogSingle } from "../../../api/Blogs";
import { useParams } from "react-router-dom";

// Loader Context
import { useLoader } from "../../../contexts/LoaderContext";

const BlogDetail = () => {
  const classes = useStyles();

  // component states
  const [blog, setBlog] = useState(null);
  const [recentBlogs, setRecentBlogs] = useState([]);
  const [, setError] = useState("");
  const [id, setID] = useState(null);

  // const { id } = useParams();
  const { title } = useParams();

  // contexts
  const { startLoader, cancelLoader } = useLoader();

  useEffect(() => {
    if (title) {
      startLoader();

      // Fetch blog with the specified title
      FetchBlogSingle(title)
        .then((res) => {
          if (res.data.blog) {
            setBlog(res.data.blog);
            setID(res.data.blog._id);
          }

          if (res.data.error) {
            setError(res.data.error);
          }
          cancelLoader();
        })
        .catch((err) => {
          console.log("Fetch Blog Error: ", err);
          setError(err);

          cancelLoader();
        });
    }
  }, [title]);

  useEffect(() => {
    if (id) {
      startLoader();

      FetchBlogsRecent(id)
        .then((res) => {
          // window.scrollTo(0, 0);
          // strip html from the blogs content
          const renderRecentBlogs = res.data.blogs.map((blog) => {
            const strippedTagsContent = blog.content.replace(/<[^>]+>/g, "");

            const cleanBlog = {
              ...blog,
              content: strippedTagsContent,
            };

            return cleanBlog;
          });

          setRecentBlogs(renderRecentBlogs);

          cancelLoader();
        })
        .catch((err) => {
          console.log("Recent error: ", err);

          cancelLoader();
        });
    }
  }, [id]);

  return (
    blog && (
      <>
        <Helmet>
          <title>{`${blog.title} : EasySocial`}</title>
        </Helmet>
        <Header />
        <BlogDetails blog={blog} />

        {/* <Subscription />  */}
        <SignUp />

        {/* Some other blogs */}
        {recentBlogs.length > 0 && (
          <section className={classes["blog-list"]}>
            <div className="container">
              {recentBlogs.length > 0 && (
                <BlogList page="BLOG_DETAILS" recentBlogs={recentBlogs} />
              )}
            </div>
          </section>
        )}
        <Footer />
      </>
    )
  );
};

export default BlogDetail;
