import React from "react";
// react-router-dom
import { Link } from "react-router-dom";
// mui imports
import { Grid } from "@material-ui/core";

// utils imports
import FormatBlogTitle from "../utils/FormatBlogTitle";

const LatestBlogs = ({ latestBlogs }) => {
  return (
    <Grid container spacing={2} className="blogs">
      {latestBlogs.map((blog, index) => (
        <Grid item xs={12} md={6} lg={index === 0 ? 7 : 5} key={index}>
          <div className="card-outer">
            <div className={"card"}>
              <Link
                to={`/blogs/${FormatBlogTitle(blog.title)}`}
                className="picture"
              >
                {/* Image */}
                {blog.coverPicture && (
                  <img src={blog.coverPicture} alt="Placeholder" />
                )}
              </Link>
              <div className="info">
                {blog.category && blog.category !== "null" && (
                  <strong className="category">{blog.category}</strong>
                )}
                <Link
                  className="title"
                  to={`/blogs/${FormatBlogTitle(blog.title)}`}
                >
                  {blog.title}
                </Link>
                <div className="data">
                  <div className="data">
                    {blog.author && <p className="author">{"Sam Vincent"}</p>}
                    <p className="date">
                      {new Date(blog.createdAt).toDateString()}
                    </p>
                  </div>
                </div>
                <p className="desc">
                  {`${blog.content
                    .split(" ")
                    .splice(0, index === 0 ? 57 : 38)
                    .join(" ")}...`}
                </p>
              </div>
            </div>
          </div>
        </Grid>
      ))}
    </Grid>
  );
};

export default LatestBlogs;
