import axios from "axios";

import { API } from "../../Backend";

const ENV = "production";

let BE_URL = null;

switch (ENV) {
  case "development":
    BE_URL = "http://localhost:5000/api";
    break;

  case "production":
    BE_URL = API;
    break;

  default:
    BE_URL = null;
}

const FetchBlogsAll = async () => {
  try {
    const FetchBlogsRes = await axios({
      url: `${BE_URL}/blogs/`,
    });

    return {
      data: FetchBlogsRes.data,
    };
  } catch (error) {
    return {
      error: error.message,
    };
  }
};

const FetchBlog = async (blogID) => {
  try {
    const FetchBlogRes = await axios({
      url: `${BE_URL}/blogs/${blogID}`,
    });

    return {
      data: FetchBlogRes.data,
    };
  } catch (error) {
    return {
      error: error.message,
    };
  }
};

