import React from "react";
// react-router-dom
import { Link } from "react-router-dom";
// mui imports
import { Grid } from "@material-ui/core";

// utils imports
import FormatBlogTitle from "../utils/FormatBlogTitle";

const RemainingBlogs = ({ remainingBlogs }) => {
  return (
    <Grid container spacing={3} className="blogs">
      {remainingBlogs.map((blog, index) => (
        <Grid item xs={12} md={4} key={index}>
          <div className="card-outer">
            <div className={"card"}>
              <Link
                to={`/blogs/${FormatBlogTitle(blog.title)}`}
                className="picture"
              >
                {/* Image */}
                {blog.coverPicture && (
                  <img src={blog.coverPicture} alt="Placeholder" />
                )}
              </Link>
              <div className="info">
                {blog.category && blog.category !== "null" && (
                  <strong className="category">{blog.category}</strong>
                )}
                <Link
                  className="title"
                  to={`/blogs/${FormatBlogTitle(blog.title)}`}
                >
                  {blog.title}
                </Link>
                <div className="data">
                  {blog.author && <p className="author">{"Sam Vincent"}</p>}
                  <p className="date">
                    {new Date(blog.createdAt).toDateString()}
                  </p>
                </div>
                <p className="desc">
                  {`${blog.content.split(" ").splice(0, 38).join(" ")}...`}
                </p>
              </div>
            </div>
          </div>
        </Grid>
      ))}
    </Grid>
  );
};

export default RemainingBlogs;
