import React, { useCallback, useEffect, useState } from "react";
import PhoneInput from "react-phone-input-2";

import useLocalStorage from "../../../hooks/useLocalStorage";

import "react-phone-input-2/lib/material.css";
import useStyles from "./styles/muiStyles";

const SignUp = () => {
  const { "sign-up": signUpClass } = useStyles(),
    [country, setCountry] = useLocalStorage("country", null),
    [phoneNo, setPhoneNo] = useState(""),
    getCountry = useCallback(async () => {
      try {
        const visitorReq = await fetch("//api.ipregistry.co/?key=tryout", {
          method: "GET",
        });
        if (visitorReq.ok) {
          const visitor = await visitorReq.json();
          setCountry(visitor?.location?.country?.code.toLowerCase());
        } else {
          setCountry("in");
        }
      } catch (error) {
        console.error(error);
      }
    }, [setCountry]),
    handleSubmit = (event) => {
      event.preventDefault();
      const {
        target: {
          elements: { phone },
        },
      } = event;
      console.log(phone.value);
    };

  useEffect(() => {
    country === null && getCountry();
  }, [country, getCountry]);

  return (
    <section className={signUpClass}>
      <div className="content">
        <h4 className="heading">
          WhatsApp ChatBot for businesses of all sizes.
        </h4>
        {country && (
          <form className="signup" onSubmit={handleSubmit}>
            <PhoneInput
              country={country}
              value={phoneNo}
              onChange={(phone) => setPhoneNo(phone)}
              // enableAreaCodes={true}
              placeholder="Enter your phone number"
              containerClass="phone-input"
              inputProps={{
                name: "phone",
                required: true,
              }}
            />
            <button type="submit">Sign Up Free</button>
          </form>
        )}
        <ul className="features">
          <li>Free tools with a lot of features</li>
          <li>No credit card required</li>
        </ul>
      </div>
    </section>
  );
};

export default SignUp;
