import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
// Font awesome
import FontAwesome from "react-fontawesome";
// material-ui imports
import { useMediaQuery } from "@material-ui/core";
// theme
import { useTheme } from "@material-ui/core/styles";

// styles
import useStyles from "./styles/muiStyles";

// mui imports
import { Hidden, Typography } from "@material-ui/core";

import parse from "html-react-parser";

const BlogDetails = ({ blog }) => {
  const theme = useTheme(), // theme setup
    isMobile = useMediaQuery(theme.breakpoints.down("sm")), // media query breakpoints
    history = useHistory(),
    classes = useStyles(),
    [url, setURL] = useState("");

  useEffect(() => {
    if (typeof window !== "undefined") {
      window.scrollTo(0, 0);
      setURL(window.location.href);
      const App = document.querySelector("#root .App"),
        header = document.querySelector(".data .header");
      App.addEventListener("scroll", (e) =>
        isMobile
          ? App.scrollTop > 485
            ? header.classList.add("stick-it")
            : header.classList.remove("stick-it")
          : App.scrollTop > 700
          ? header.classList.add("stick-it")
          : header.classList.remove("stick-it")
      );
    }
  }, [blog, isMobile]);

  return (
    <>
      {blog && (
        <section className={classes["blog-detail"]}>
          <div className="container">
            <div className="blog">
              <div className="picture">
                {blog.coverPicture && (
                  <img
                    src={blog.coverPicture}
                    alt="placeholder"
                    className={classes.img}
                  />
                )}
              </div>
              <div className="data">
                <div className="header">
                  <div className="left">
                    <button
                      type="button"
                      className="go-back"
                      onClick={() => history.goBack()}
                    >
                      <svg
                        width="36"
                        height="21"
                        viewBox="0 0 36 21"
                        fill="none"
                      >
                        <path
                          d="M11.5 2L3 10.5L11.5 19"
                          stroke="black"
                          strokeWidth="3"
                        />
                        <path
                          d="M3.39526 10.5H35.4185"
                          stroke="black"
                          strokeWidth="3"
                        />
                      </svg>
                    </button>
                    <p className="title">{blog.title}</p>
                  </div>
                  <Hidden mdDown implementation="js">
                    <div className="social-share">
                      <p className="label">SHARE</p>
                      <ul className="medias">
                        <li className="item">
                          <a
                            href={encodeURI(
                              `https://www.facebook.com/sharer/sharer.php?u=${url}`
                            )}
                            className="link"
                            aria-label="Share on Facebook"
                            target="_blank"
                            rel="noreferrer"
                          >
                            <FontAwesome
                              className={"fab fa-facebook-f"}
                              name="facebook"
                            />
                          </a>
                        </li>
                        <li className="item">
                          <a
                            href={encodeURI(
                              `https://twitter.com/intent/tweet?url=${url}`
                            )}
                            className="link"
                            aria-label="Share on Twitter"
                            target="_blank"
                            rel="noreferrer"
                          >
                            <FontAwesome
                              className={"fab fa-twitter"}
                              name="twitter"
                            />
                          </a>
                        </li>
                        <li className="item">
                          <a
                            href={encodeURI(
                              `https://www.linkedin.com/sharing/share-offsite/?url=${url}`
                            )}
                            className="link"
                            aria-label="Share on Linked In"
                          >
                            <FontAwesome
                              className={"fab fa-linkedin"}
                              name="linkedin"
                            />
                          </a>
                        </li>
                      </ul>
                    </div>
                  </Hidden>
                </div>
                <Hidden mdUp implementation="js">
                  <div className="social-share">
                    <ul className="medias">
                      <li className="item">
                        <a
                          href={encodeURI(
                            `https://www.facebook.com/sharer/sharer.php?u=${url}`
                          )}
                          className="link"
                          aria-label="Share on Facebook"
                          target="_blank"
                          rel="noreferrer"
                        >
                          <FontAwesome
                            className={"fab fa-facebook-f"}
                            name="facebook"
                          />
                        </a>
                      </li>
                      <li className="item">
                        <a
                          href={encodeURI(
                            `https://twitter.com/intent/tweet?url=${url}`
                          )}
                          className="link"
                          aria-label="Share on Twitter"
                          target="_blank"
                          rel="noreferrer"
                        >
                          <FontAwesome
                            className={"fab fa-twitter"}
                            name="twitter"
                          />
                        </a>
                      </li>
                      <li className="item">
                        <a
                          href={encodeURI(
                            `https://www.linkedin.com/sharing/share-offsite/?url=${url}`
                          )}
                          className="link"
                          aria-label="Share on Linked In"
                        >
                          <FontAwesome
                            className={"fab fa-linkedin"}
                            name="linkedin"
                          />
                        </a>
                      </li>
                    </ul>
                  </div>
                </Hidden>
                <div className="info">
                  {blog.category && blog.category !== "null" && (
                    <p className="category">
                      <strong>{blog.category}</strong>
                    </p>
                  )}
                  <h1 className="title">{blog.title}</h1>
                  {blog.author && <p className="author">{classes.author}</p>}
                  <p className="date">
                    {new Date(blog.createdAt).toDateString()}
                  </p>
                </div>
                <Typography component={"div"} className="content">
                  {parse(blog.content)}
                </Typography>
              </div>
            </div>
          </div>
        </section>
      )}
    </>
  );
};

export default BlogDetails;
