import { makeStyles } from "@material-ui/core";

// menu classes
const useStyles = makeStyles((theme) => ({
  "sign-up": {
    alignItems: "center",
    backgroundColor: "#12B0EE",
    display: "flex",
    justifyContent: "center",
    height: "410px",
    padding: "0 26px",
    position: "relative",
    textAlign: "center",
    zIndex: 99,

    "& .content": {
      display: "flex",
      flexDirection: "column",
      margin: "0 auto",
      maxWidth: "530px",

      "& h4": {
        color: "#ffffff",
        fontSize: "36px",
        fontWeight: 900,
        lineHeight: "42px",
        marginBottom: "20px",
        textShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
      },

      "& .signup": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        marginBottom: "25px",

        "& .phone-input": {
          width: "auto!important",
          marginBottom: "10px",

          "& .flag-dropdown": {
            left: "0!important",

            "& .selected-flag .flag": {
              marginTop: "-12px !important",
              top: "50%!important",
            },
          },

          "& input": {
            backgroundColor: "#ffffff!important",
            border: "1px solid #12B0EE!important",
            boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
            borderRadius: "8px!important",
            fontSize: "16px!important",
            fontWeight: 500,
            height: "48px!important",
            lineHeight: "24px!important",
            paddingTop: "0!important",
            paddingBottom: "0!important",
            width: "320px!important",
          },
        },

        "& button": {
          alignItems: "center",
          backgroundColor: "#F8D44D",
          boxShadow: "0px 4px 4px rgba(0, 0, 0, 0.25)",
          color: "#24272C",
          cursor: "pointer",
          display: "flex",
          height: "48px",
          justifyContent: "center",
          marginLeft: 0,
          padding: 0,
          width: "125px",
        },
      },

      "& .features": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        listStyle: "none",

        "& li": {
          alignItems: "center",
          color: "#FFFFFF",
          display: "flex",
          fontSize: "16px",
          fontWeight: 500,
          justifyContent: "center",
          lineHeight: "24px",
          paddingLeft: "22px",
          position: "relative",

          "&:not(:last-child)": {
            marginBottom: "12px",
          },

          "&::before": {
            content: '""',
            backgroundImage:
              'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M2%206.85802L5.50215%2011.0001L14%202.00006%22%20stroke%3D%22white%22%20stroke-width%3D%223%22%2F%3E%3C%2Fsvg%3E")',
            height: "13px",
            left: 0,
            position: "absolute",
            width: "16px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {},
  [theme.breakpoints.up("md")]: {
    "sign-up": {
      height: "410px",

      "& .content": {
        "& h4": {
          fontSize: "48px",
          lineHeight: "48px",
          marginBottom: "40px",
        },

        "& .signup": {
          flexDirection: "row",

          "& .phone-input": {
            marginBottom: 0,

            "& input": {
              marginBottom: 0,
              width: "363px!important",
            },
          },

          "& button": {
            marginLeft: "10px",
            width: "135px",
          },
        },

        "& .features": {
          flexDirection: "row",
          justifyContent: "center",

          "& li": {
            "&:not(:last-child)": {
              marginBottom: 0,
              marginRight: "28px",
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {},
  [theme.breakpoints.up("xl")]: {},
}));

export default useStyles;
