import { useEffect, useState } from "react";
// react-router-dom
import { Link } from "react-router-dom";
// mui imports
import { Grid } from "@material-ui/core";

// component imports
import LatestBlogs from "./LatestBlogs";
import RemainingBlogs from "./RemainingBlogs";

import useStyles from "./styles/muiStyles";

// request helpers
import { FetchBlogsAll } from "../../../../api/Blogs";

// utils imports
import FormatBlogTitle from "./utils/FormatBlogTitle";

// Loader context
import { useLoader } from "../../../../contexts/LoaderContext";

const BlogList = ({ page, recentBlogs }) => {
  const classes = useStyles();
  const [latest, setLatest] = useState([]);
  const [remains, setRemains] = useState([]);

  // component states
  const [setBlogs] = useState([]);

  // contexts
  const { startLoader, cancelLoader } = useLoader();

  // component life cycles
  useEffect(() => {
    startLoader();

    FetchBlogsAll()
      .then((res) => {
        const { blogs } = res.data;

        if (blogs.length) {
          const renderBlogs = blogs.map((blog) => {
            const strippedTagsContent = blog.content.replace(/<[^>]+>/g, "");

            const cleanBlog = {
              ...blog,
              content: strippedTagsContent,
            };

            return cleanBlog;
          });
          const [blog1, blog2, ...remaining] = renderBlogs;
          setLatest([blog1, blog2]);
          setRemains(remaining);

          setBlogs(renderBlogs);

          cancelLoader();
        }
      })
      .catch((err) => {
        console.log("Fetch Blogs Error: ", err);
        cancelLoader();
      });
  }, []);

  return (
    <>
      {page === "BLOG_DETAILS" ? (
        <Grid container spacing={2} className="blogs">
          {recentBlogs.map((blog) => (
            <Grid item xs={12} md={4} key={blog._id}>
              <div className="card-outer">
                <div className={"card"}>
                  <Link
                    to={`/blogs/${FormatBlogTitle(blog.title)}`}
                    className="picture"
                  >
                    {/* Image */}
                    {blog.coverPicture && (
                      <img src={blog.coverPicture} alt="Placeholder" />
                    )}
                  </Link>
                  <div className="info">
                    {blog.category && blog.category !== "null" && (
                      <strong className="category">{blog.category}</strong>
                    )}
                    <Link
                      className="title"
                      to={`/blogs/${FormatBlogTitle(blog.title)}`}
                    >
                      {blog.title}
                    </Link>
                    <div className="data">
                      <div className="data">
                        {blog.author && (
                          <p className="author">{"Sam Vincent"}</p>
                        )}
                        <p className="date">
                          {new Date(blog.createdAt).toDateString()}
                        </p>
                      </div>
                    </div>
                    <p className="desc">
                      {`${blog.content.split(" ").splice(0, 38).join(" ")}...`}
                    </p>
                  </div>
                </div>
              </div>
            </Grid>
          ))}
        </Grid>
      ) : (
        <section className={classes["blog-list"]}>
          <div className="container">
            {/* Latest Blogs */}
            {latest.length > 1 && <LatestBlogs latestBlogs={latest} />}
            {/* Other Blogs */}
            {remains.length > 0 && <RemainingBlogs remainingBlogs={remains} />}
          </div>
        </section>
      )}
    </>
  );
};

export default BlogList;
