import { makeStyles } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  "blog-list": {
    paddingTop: "56px",

    "& .container": {
      margin: "0 auto",
      padding: 0,

      "& .blogs": {
        marginTop: "30px",

        "&:first-child": {
          marginTop: "45px",
        },

        "&:last-child": {
          marginBottom: "45px",
        },

        "& .card-outer": {
          border: "3px solid transparent",
          borderRadius: "14px",
          height: "100%",
          transition: "all 0.2s ease",
          width: "100%",

          "&:hover": {
            borderColor: "#12B0EE",
            boxShadow: "0px 0px 30px rgba(126, 210, 255, 0.5)",
          },
        },
        "& .card": {
          backgroundColor: "#ffffff",
          borderRadius: "12px",
          boxShadow: "0px 0px 30px rgba(126, 210, 255, 0.25)",
          display: "flex",
          flexDirection: "column",
          height: "100%",
          overflow: "hidden",
          position: "relative",
          width: "100%",

          "& .picture": {
            backgroundColor: "rgba(36, 39, 44, 0.7)",
            height: "290px",
            overflow: "hidden",

            "& img": {
              objectFit: "cover",
              objectPosition: "top center",
              width: "100%",
              maxHeight: "290px",
            },
          },

          "& .info": {
            padding: "16px",

            "& .category": {
              color: "#12B0EE",
              display: "inline-flex",
              fontWeight: 700,
              fontSize: "14px",
              lineHeight: "20px",
              marginBottom: "10px",
            },

            "& .title": {
              color: "#24272C",
              display: "block",
              fontWeight: 900,
              fontSize: "28px",
              lineHeight: "28px",
              textDecoration: "none",
              marginBottom: "10px",
            },

            "& .data": {
              display: "flex",
              justifyContent: "space-between",
              marginBottom: "10px",
              width: "100%",

              "& .author": {
                color: "#24272C",
                fontWeight: 700,
                fontSize: "16px",
                lineHeight: "20px",
              },

              "& .date": {
                color: "#24272C",
                fontWeight: 500,
                fontSize: "16px",
                lineHeight: "20px",
                marginLeft: "auto",
                opacity: 0.7,
              },
            },

            "& .desc": {
              color: "#24272C",
              fontWeight: 300,
              fontSize: "18px",
              lineHeight: "24px",
              opacity: 0.7,
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {
    "blog-list": {
      "& .container": {
        width: "95%",
      },
    },
  },
  [theme.breakpoints.up("sm")]: {
    "blog-list": {
      "& .container": {
        width: "90%",
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "blog-list": {
      paddingTop: "67px",

      "& .container": {
        width: "80%",

        "& .blogs": {
          "&:first-child": {
            marginTop: "100px",
          },

          "&:last-child": {
            marginBottom: "60px",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    "blog-list": {
      "& .container": {
        width: "80%",
      },
    },
  },
  [theme.breakpoints.up("xl")]: {
    "blog-list": {
      "& .container": {
        maxWidth: "70%",
      },
    },
  },
  links: {
    textDecoration: "none",
    color: "#000",
    "&:hover": {
      textDecoration: "none",
      color: "#000",
    },
    "&:active": {
      textDecoration: "none",
      color: "#000",
    },
    "&:visited": {
      textDecoration: "none",
      color: "#000",
    },
  },
}));

export default useStyles;
