import { makeStyles } from "@material-ui/core";

// menu classes

const useStyles = makeStyles((theme) => ({
  "blog-detail": {
    paddingTop: "56px",

    "& .container": {
      margin: "0 auto",
      padding: 0,

      "& .blog": {
        marginTop: "45px",
        marginBottom: "50px",

        "& .picture": {
          backgroundColor: "rgba(36, 39, 44, 0.7)",
          borderRadius: "12px 12px 0px 0px",
          display: "flex",
          margin: "0 -2.5%",
          overflow: "hidden",

          "& img": {
            width: "100%",
            objectFit: "cover",
            objectPosition: "top center",
            maxHeight: "290px",
          },
        },

        "& .data": {
          margin: "35px auto 0",
          width: "100%",
          background: "#FFFFFF",
          borderRadius: "4px",
          padding: "0 16px",
          position: "relative",

          "& .header": {
            alignItems: "center",
            background: "#FFFFFF",
            borderRadius: "4px",
            display: "flex",
            justifyContent: "space-between",
            width: "100%",

            "&.stick-it": {
              boxShadow: "0px 8px 30px -15px rgba(0, 0, 0, 0.5)",
              position: "sticky",
              top: "55px",
              width: "100vw",
              left: "50%",
              transform: "translateX(-26px)",
              height: "66px",
              padding: "14px 26px",
              zIndex: 99,

              "& .left": {
                "& .title": {
                  display: "block",
                },
              },
            },

            "& .left": {
              alignItems: "center",
              display: "flex",

              "& .go-back": {
                alignItems: "center",
                backgroundColor: "transparent",
                display: "flex",
                justifyContent: "center",
                height: "34px",
                cursor: "pointer",
                margin: 0,
                padding: 0,
              },

              "& .title": {
                color: "#24272C",
                display: "none",
                fontWeight: 900,
                fontSize: "18px",
                lineHeight: "22px",
                marginLeft: "10px",
              },
            },

            "& .social-share": {
              alignItems: "center",
              display: "flex",

              "& .label": {
                color: "#24272C",
                fontWeight: 400,
                fontSize: "12px",
                lineHeight: "20px",
                marginRight: "10px",
              },

              "& .medias": {
                display: "flex",
                listStyle: "none",

                "& .item": {
                  "&:not(:last-child)": {
                    marginRight: "10px",
                  },

                  "& .link": {
                    alignItems: "center",
                    backgroundColor: "#12B0EE",
                    borderRadius: "50%",
                    color: "#ffffff",
                    display: "flex",
                    height: "34px",
                    justifyContent: "center",
                    overflow: "hidden",
                    textDecoration: "none",
                    width: "34px",

                    "& .fab": {
                      fontSize: "22px",
                    },
                  },
                },
              },
            },
          },

          "& > .social-share": {
            alignItems: "center",
            background: "#FFFFFF",
            boxShadow: "0px 0px 10px rgba(0, 0, 0, 0.25)",
            borderRadius: "10px 0px 0px 10px",
            display: "flex",
            height: "123px",
            justifyContent: "center",
            position: "fixed",
            right: "5px",
            top: "500px",
            width: "39px",
            zIndex: 999,

            "& .medias": {
              display: "flex",
              listStyle: "none",
              flexDirection: "column",

              "& .item": {
                "&:not(:last-child)": {
                  marginBottom: "10px",
                },

                "& .link": {
                  alignItems: "center",
                  backgroundColor: "#12B0EE",
                  borderRadius: "50%",
                  color: "#ffffff",
                  display: "flex",
                  height: "24px",
                  justifyContent: "center",
                  overflow: "hidden",
                  textDecoration: "none",
                  width: "24px",

                  "& .fab": {
                    fontSize: "12px",
                  },
                },
              },
            },
          },

          "& .info": {
            marginTop: "24px",
            marginBottom: "16px",

            "& .category": {
              marginBottom: "10px",

              "& strong": {
                color: "#12B0EE",
                fontWeight: 700,
                fontSize: "14px",
                lineHeight: "20px",
              },
            },

            "& .title": {
              color: "#24272C",
              fontWeight: 900,
              fontSize: "28px",
              lineHeight: "28px",
              marginBottom: "34px",
            },

            "& .author": {
              color: "#24272C",
              fontWeight: 700,
              fontSize: "16px",
              lineHeight: "20px",
            },

            "& .date": {
              color: "#24272C",
              fontWeight: 500,
              fontSize: "16px",
              lineHeight: "20px",
              opacity: 0.7,
            },
          },

          "& .content": {
            "& > *": {
              color: "#24272C",
              fontWeight: 300,
              fontSize: "18px",
              lineHeight: "24px",
              opacity: 0.7,
            },

            "& p": {
              marginBottom: "24px",
            },

            "& h1,& h2,& h3,& h4,& h5,& h6": {
              fontWeight: 600,
              opacity: 1,
            },

            "& strong": {
              fontWeight: 600,
            },

            "& ul, & ol": {
              paddingLeft: "40px",
              marginBottom: "24px",
            },

            "& a": {
              color: "#12B0EE",
              opacity: 0.8,
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {
    "blog-detail": {
      "& .container": {
        width: "95%",
      },
    },
  },
  [theme.breakpoints.up("sm")]: {
    "blog-detail": {
      "& .container": {
        width: "90%",
        margin: "0 auto",
      },
    },
  },
  [theme.breakpoints.up("md")]: {
    "blog-detail": {
      paddingTop: "67px",

      "& .container": {
        width: "80%",
        margin: "0 auto",

        "& .blog": {
          marginBottom: "140px",

          "& .picture": {
            margin: 0,

            "& img": {
              maxHeight: "480px",
            },
          },

          "& .data": {
            marginTop: "-100px",
            maxWidth: "1100px",
            padding: "45px 60px",

            "& .header": {
              "&.stick-it": {
                top: "66px",
                width: "1148px",
                left: "50%",
                transform: "translateX(-84px)",
                height: "63px",
                padding: "14px 20px",
              },

              "& .left": {
                "& .title": {
                  color: "#12B0EE",
                  fontSize: "18px",
                  lineHeight: "50px",
                  marginLeft: "20px",
                },
              },
            },

            "& .info": {
              marginTop: "30px",
              marginBottom: "60px",

              "& .category": {
                marginBottom: "16px",

                "& strong": {
                  fontSize: "18px",
                },
              },

              "& .title": {
                color: "#24272C",
                fontWeight: 900,
                fontSize: "50px",
                lineHeight: "48px",
                marginBottom: "35px",
              },

              "& .author": {
                fontSize: "20px",
                marginBottom: "10px",
              },

              "& .date": {
                fontSize: "18px",
              },
            },

            "& .content": {
              "& > *": {
                fontSize: "24px",
                lineHeight: "32px",
              },

              "& p": {
                marginBottom: "32px",
              },

              "& ul, & ol": {
                marginBottom: "32px",
              },
            },
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    "blog-detail": {
      "& .container": {
        width: "80%",
        margin: "0 auto",
      },
    },
  },
  [theme.breakpoints.up("xl")]: {
    "blog-detail": {
      "& .container": {
        maxWidth: "70%",
        margin: "0 auto",
      },
    },
  },
}));

export default useStyles;
